import { Component, OnInit } from '@angular/core';
import { RequeteService } from '../../services/requete.service';
import { ActivatedRoute, Router } from "@angular/router";
@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.scss']
})
export class SearchComponent implements OnInit {
public taille = '100%';
public activate = false;
private scrollL=0;
private scrollW=0;
public carte:any=[];
public carteZOOM:any=[];
public q: string="";
public tab:any=[];
public modeles:any=[];
public cat = "all";
public load = false;
  constructor(public requete: RequeteService, private route: ActivatedRoute, private router: Router) { }

  ngOnInit() {
	    this.route.paramMap.subscribe(params => {
			this.q = params.get("crypt");
			this.cat = params.get("cat");
			this.changeparams(params.get("cat"), params.get("cat"), 'alto', params.get("crypt"));
			this.getSearch('');
			this.modelesCarte();

  });
	  if(this.tab.length > 10){
			   this.scrollW = 15000;
			   this.scrollL = 0;
	  }else{
			   this.scrollW = 0;
			   this.scrollL = 0;
	  }
	  document.addEventListener('DOMContentLoaded', (event) => {
  //the event occurred
	  this.getW();
});

  }

  async changeparams(params, id, classname, el){
	  this.tab = [];
	  this.carte = [];
	  this.load = false;
		let x = document.getElementsByClassName(classname);
		for (let i = 0; i < x.length; i++) {
			x[i].classList.remove('active');
		}

	  let elem = document.getElementById(id);
	  elem.classList.add('active');
	  if ( elem.classList.contains('active') ) {
			  this.cat = await params;
			  if(el != ""){

			  this.q = await el;

			  }
			  let apKey = new Date().getTime() + 'Togetme+research+motor+activated+valeur+accords';
			  this.router.navigate(['/search/'+this.cat+'/'+this.q], {relativeTo: this.route,  queryParams: { order: 'popular', Crypt: apKey, 'OverView': true, 'BiheviorSubject': false, 'asyncr': true, 'return_OMEGA':true, 'access': true, 'private':true } });
			  // this.getSearch(this.q);
		}
  }

  getSearch(param=""){
	  this.carte = [];
	  this.load = false;
	  	    this.route.paramMap.subscribe(params => {
			this.q = params.get("crypt");
			this.cat = params.get("cat");

					  if(param != ""){
						  console.log(1);
						  this.q = param;
						  let apKey = new Date().getTime() + 'Togetme+research+motor+activated+valeur+accords';
						  this.router.navigate(['/search/'+this.cat+'/'+this.q], {relativeTo: this.route,  queryParams: { order: 'popular', Crypt: apKey, 'OverView': true, 'BiheviorSubject': false, 'asyncr': true, 'return_OMEGA':true, 'access': true, 'private':true  } });

					  }
							switch(this.cat){
								case 'all' :
											this.tab = [];
										  this.requete.getAll(this.q).subscribe((resultat)=>{
											  this.carte = resultat;
											  console.log(this.carte);
											  this.load = true;
										  },(error)=>{});

								break;


								case 'entreprise' :
											this.tab = [];
										  this.requete.getE(this.q).subscribe((resultat)=>{
											  this.carte = resultat;
											  this.tab = resultat['service'];
											  console.log(this.carte);
											  this.load = true;
										  });

								break;

								case 'particulier' :
										this.tab = [];
										  this.requete.getP(this.q).subscribe((resultat)=>{
											  this.carte = resultat;
											  console.log(this.carte);
											  this.load = true;
										  });

								break;

								case 'ong' :
										this.tab = [];
										  // this.requete.get(this.q).subscribe((resultat)=>{
											  // this.carte = resultat;
											  // console.log(this.carte);
											  this.load = true;
										  // });
										  this.carte = [];

								break;

								default :
										this.tab = [];

											  this.carte = [];
											 this.load = true;

								break;

							}



			});
  }


	modelesCarte(){
		this.requete.getModeles().subscribe((resultat)=>{
		this.modeles = resultat;
				console.log(this.modeles);
		});

		}
  show(){
	  this.activate = true;
	  this.taille = '50%';
  }

  hide(){
	  this.activate = false;
	  this.taille = '100%';

  }

  getW(){

	  let element = document.getElementById('togetme_parent');
	  if(element){

		  element.addEventListener("scroll", (event:any) => {
				   this.scrollW = event.target.scrollLeftMax;
				   this.scrollL = event.target.scrollLeft;
		  });
	  }
  }

  scrollLateral(coord_x, coord_y, pos){
	 let element = document.getElementById('togetme_parent');
	  let taille = element.offsetWidth;
	  let taillescroll = element.scrollWidth;
	  this.scrollW = taillescroll;
	  if(pos === 'left'){

			  if(this.scrollL < taillescroll){
				  if((this.scrollL) < 0){
					  this.scrollL = 0;
				  }
						this.scrollL = this.scrollL + taille - 50;

			  }

	  }else if(pos === 'right'){
		  if((this.scrollL) >= taillescroll){
			  this.scrollL = taillescroll - taille;
		  }
					this.scrollL = this.scrollL - taille  + 10 ;

	  }else{
		   this.scrollL = taille;
	  }
	  element.scrollTo(this.scrollL,coord_y);

  }


  async transf(pram){
	  this.carteZOOM = [];
	this.carteZOOM = await pram;

  }

}
